	module.exports = {"data":{"markdownRemark":{"html":"<ul>\n<li>Linux now operates on the majority of smartphones and tablets around the world via the <a href='https://en.wikipedia.org/wiki/Android_(operating_system' target='_blank' rel='nofollow'>Android</a>) OS (based off the Linux kernel). As of March 2016 Android holds <a href='https://www.netmarketshare.com/operating-system-market-share.aspx?qprid=8&qpcustomd=1' target='_blank' rel='nofollow'>60.99%</a> of the market share on mobile devices (tablets and smartphones). It also holds <a href='http://www.idc.com/prodserv/smartphone-os-market-share.jsp' target='_blank' rel='nofollow'>82.8%</a> for smartphones alone (per 2015 Q2 numbers).</li>\n<li>As of March 2016, Alexa reported that <a href='http://www.w3cook.com/os/summary/' target='_blank' rel='nofollow'>96.55%</a> of the top 1 million Internet domains in the world are powered by servers running Linux.</li>\n<li>The <a href='https://en.wikipedia.org/wiki/New_York_Stock_Exchange' target='_blank' rel='nofollow'>New York Stock Exchange (NYSE)</a>, the largest stock exchange in the world which trades securities worth billions of dollars daily are executed by servers that run on Linux after moving away from UNIX.</li>\n<li>The White House powers their web servers using <a href='https://en.wikipedia.org/wiki/Red_Hat' target='_blank' rel='nofollow'>Red Hat Linux</a> and <a href='https://en.wikipedia.org/wiki/Apache_HTTP_Server' target='_blank' rel='nofollow'>Apache</a>.</li>\n<li>The US Department of Defense (DoD) uses Linux to run their nuclear submarine fleet.</li>\n<li>The US Navy uses Linux for ground control software.</li>\n<li>Pakistan is using Linux in many of its public schools and colleges and plans to ultimately run it on all of its government computer systems</li>\n<li>The Sony Playstation 3 gaming console had previously made it possible to run Linux on a seperate hard drive partion. Scientific entities networked together consoles for powerful clusters. <a href='https://en.wikipedia.org/wiki/PlayStation_3_cluster' target='_blank' rel='nofollow'>The United States Air Force Research Laboratory</a> assembled a super computer by cluster 1760 Playstation 3 Consoles.</li>\n</ul>\n<p>Other adoptions of Linux from around the world can be viewed <a href='https://en.wikipedia.org/wiki/List_of_Linux_adopters' target='_blank' rel='nofollow'>here</a> and <a href='http://www.comparebusinessproducts.com/fyi/50-places-linux-running-you-might-not-expect' target='_blank' rel='nofollow'>here</a></p>","fields":{"slug":"/linux/some-usages-for-linux/"},"frontmatter":{"title":"Some Usages for Linux"}}},"pathContext":{"slug":"/linux/some-usages-for-linux/"}}

